<template>
  <tabbar v-model="active">
    <template v-for="({icon,desc,name}, index) in tabs">
      <tabbar-item
        :key="index"
        :icon="icon"
        :to="{name}"
      >
        {{desc}}
      </tabbar-item>
    </template>
  </tabbar>
</template>

<script>
import { Tabbar, TabbarItem } from 'vant'
export default {
  components: { Tabbar, TabbarItem },
  data () {
    return {
      active: 0,
      tabs: [
        {
          icon: 'lm-ketang',
          desc: '课堂',
          name: 'Classroom'
        },
        {
          icon: 'lm-zhanghu',
          desc: '我的',
          name: 'Mine'
        }
      ]
    }
  },
  watch: {
    $route () {
      this.activeTabBar()
    }
  },
  mounted () {
    this.activeTabBar()
  },
  methods: {
    activeTabBar () {
      const routeName = this.$route.name
      if (routeName === 'Classroom') {
        this.active = 0
      } else if (routeName === 'Mine') {
        this.active = 1
      } else {
        this.active = 0
      }
    }
  }
}
</script>
