/* eslint-disable no-unused-vars */
import {
  getBalance,
  getTopTeacherRefererBalanceDetail,
  getTopTeacherBalanceDetail
} from '@/api/balance'

const balance = {
  state:{
    balance:0
  },
  mutations: {
    balanceChange: (state, balance) => {
      state.balance = balance
    }
  },
  actions: {
    balanceChange ({commit}, balance) {
      commit('balanceChange', balance)
    },
    getBalance({
      commit
    }) {
      return new Promise((resolve, reject) => {
        getBalance()
          .then((data) => {
            resolve(data.Data)
          })
          .catch((err) => {
            reject(err)
          })
      })
    },
    getTopTeacherBalanceDetail({
      commit
    }, pageData) {
      return new Promise((resolve, reject) => {
        getTopTeacherBalanceDetail(pageData)
          .then((data) => {
            resolve(data.Data)
          })
          .catch((err) => {
            reject(err)
          })
      })
    },
    getTopTeacherRefererBalanceDetail({
      commit
    }, pageData) {
      return new Promise((resolve, reject) => {
        getTopTeacherRefererBalanceDetail(pageData)
          .then((data) => {
            resolve(data.Data)
          })
          .catch((err) => {
            reject(err)
          })
      })
    }
  },

}

export default balance
