/* eslint-disable no-unused-vars */
import {
  getTopTeacherClassList,
  getSysRules,
  getClassLesson,
  getMyRefererClassList,
  getMyRefererClassStudentList,
  getReferersInformationInfoList,
  topClassSignIn,
  getTopTeacherClassListByClassIDs,
  refererClassV2,
  getTopTeacherClassListByCode
} from '@/api/classroom'

const classroom = {
  actions: {
    getSysRules({
      commit
    }) {
      return new Promise((resolve, reject) => {
        getSysRules().then((data) => {
          resolve(data.GetSysRulesInfoDto)
        }).catch((err) => {
          reject(err)
        })
      })
    },
    getClassLesson({
      commit
    }, classId) {
      return new Promise((resolve, reject) => {
        getClassLesson(classId).then((data) => {
          resolve(data.Data)
        }).catch((err) => {
          reject(err)
        })
      })
    },
    getTopTeacherClassList({
      commit
    }, classData) {
      return new Promise((resolve, reject) => {
        getTopTeacherClassList(classData).then((data) => {
          resolve({
            data: data.Data,
            total: data.RowsCount
          })
        }).catch((err) => {
          reject(err)
        })
      })
    },
    getMyRefererClassList({
      commit
    }, refererData) {
      return new Promise((resolve, reject) => {
        getMyRefererClassList(refererData)
          .then((data) => {
            resolve({
              data: data.Data,
              total: data.RowsCount
            })
          })
          .catch((err) => {
            reject(err)
          })
      })
    },
    getMyRefererClassStudentList({
      commit
    }, MyRefererCode) {
      return new Promise((resolve, reject) => {
        getMyRefererClassStudentList(MyRefererCode)
          .then((data) => {
            resolve({
              data: data.Data,
              total: data.RowsCount
            })
          })
          .catch((err) => {
            reject(err)
          })
      })
    },
    getReferersInformationInfoList({
      commit
    }, MyRefererCode) {
      return new Promise((resolve, reject) => {
        getReferersInformationInfoList(MyRefererCode)
          .then((data) => {
            resolve({
              data: data.Data,
              total: data.RowsCount
            })
          })
          .catch((err) => {
            reject(err)
          })
      })
    },
    topClassSignIn({
      commit
    }, classData) {
      return new Promise((resolve, reject) => {
        topClassSignIn(classData).then((data) => {
          resolve(data.Message)
        }).catch((err) => {
          reject(err)
        })
      })
    },
    getTopTeacherClassListByClassIDs({
      commit
    }, classData) {
      return new Promise((resolve, reject) => {
        getTopTeacherClassListByClassIDs(classData).then((data) => {
          resolve({
            data: data.Data,
            total: data.RowsCount
          })
        }).catch((err) => {
          reject(err)
        })
      })
    },
    refererClassV2({
      commit
    }, classData) {
      return new Promise((resolve, reject) => {
        refererClassV2(classData).then((data) => {
          resolve(data.Data)
        }).catch((err) => {
          reject(err)
        })
      })
    },
    getTopTeacherClassListByCode({
      commit
    }, invitationCodeData) {
      return new Promise((resolve, reject) => {
        getTopTeacherClassListByCode(invitationCodeData).then((data) => {
          resolve({
            data: data.Data,
            total: data.RowsCount
          })
        }).catch((err) => {
          reject(err)
        })
      })
    }
  },

}

export default classroom
