<template>
  <nav-bar
    :border="false"
    class="navbar"
    :title="title"
    :left-text="left?'返回':''"
    :left-arrow="left"
    @click-left="back"
    @click-right="action"
  >
    <template v-if="right">
      <van-icon
        v-if="right.icon"
        :name="right.icon"
        slot="right"
      />
      <span
        class="navbar-right ignore"
        slot="right"
        v-if="right.name"
      >{{right.name}}</span>
    </template>
  </nav-bar>
</template>

<script>
import { NavBar } from "vant";
export default {
  components: {
    NavBar
  },
  computed: {
    navbar() {
      return this.$route.meta.navbar;
    },
    title() {
      return (
        ((this.navbar && this.navbar.title) || "龙门课堂") +
        (!this.code && this.userInfo && !this.isS
          ? `(我的学习码：${this.userInfo.MyInvitationCode})`
          : "")
      );
    },
    left() {
      return this.navbar && this.navbar.left;
    },
    right() {
      return this.navbar && this.navbar.right;
    }
  },
  methods: {
    action() {
      this.$bus.$emit(this.right.action);
    }
  }
};
</script>

<style lang="less" scoped>
.navbar {
  position: sticky;
  top: 0;
  z-index: 999;
  &-right.ignore {
    color: @white;
    font-size: 14px;
  }
}
</style>
