import Layout from '@/layout'
const classroomRouter = {
  path: '/',
  component: Layout,
  redirect: 'classroom',
  children: [{
    path: 'classroom',
    name: 'Classroom',
    component: () => import('@/views/classroom').then(m => m.default),
    meta: {
      navbar: {
        left: false,
        title: '课堂',
        right: {
          name: '分享',
          action: 'share'
        }
      },
      showBottom: true
    },
  }, {
    path: 'classroomDetail',
    name: 'ClassroomDetail',
    component: () => import('@/views/classroom/detail').then(m => m.default),
    meta: {
      navbar: {
        left: true,
        title: '课堂详情'
      },
      showBottom: false
    }
  }, {
    path: 'invitation',
    name: 'ClassroomInvitation',
    component: () => import('@/views/classroom/invitation').then(m => m.default),
    meta: {
      navbar: {
        left: false,
        title: '课堂'
      },
      showBottom: false
    }
  }, {
    path: 'classroomReferer',
    name: 'ClassroomReferer',
    component: () => import('@/views/classroom/referer').then(m => m.default),
    meta: {
      navbar: {
        title: '课堂',
      },
      showBottom: false
    },
    beforeEnter(to, from, next) {
      let role = to.query.role
      if (role) {
        if (role === 'p') {
          setNavbarData(to);
        } else {
          to.meta.navbar.left = false
        }
      } else {
        setNavbarData(to);
      }
      next()
    }
  }]
}

export default classroomRouter

function setNavbarData(to) {
  to.meta.navbar.left = true;
  to.meta.navbar.right = {
    name: '分享',
    action: 'share'
  };
}
