import {
  request
} from '@/utils/request'
import {
  TOP_SERVICE
} from './common'
export function login({
  UserName,
  Password,
  role
}) {
  return request.post(`${TOP_SERVICE}${role === 's' ? 'StuLogin' : 'Login'}`, {
    Flat: '',
    FlatSerial: '',
    FlatModel: '',
    TimeSpan: Date.parse(new Date()) / 1000,
    UserName,
    Password
  })
}

export function getUserToken({
  UserName,
  Password
}) {
  return request.post(`${TOP_SERVICE}GetUserToken`, {
    TimeSpan: Date.parse(new Date()) / 1000,
    UserName,
    Password
  })
}

export function updateUserInfo(userInfo) {
  return request.post(`${TOP_SERVICE}UpdateUserInfo`, {
    [userInfo[0]]: userInfo[1]
  })
}

export function mobileCheck(Mobile) {
  return request.post(`${TOP_SERVICE}MobileCheck`, {
    Mobile
  })
}

export function getVerifyCode(Mobile) {
  return request.post(`${TOP_SERVICE}GetVerifyCode`, {
    Mobile,
    VerifyType: 1
  })
}

export function verifyCode(mobile) {
  return request.post(`${TOP_SERVICE}VerifyCode`, {
    ...mobile,
    VerifyType: 1
  })
}

export function regedit({
  role,
  UserName,
  Password,
  RefererAccount
}) {
  return request.post(`${TOP_SERVICE}${role === 's' ? 'StuRegedit' : 'Regedit'}`, {
    Flat: '',
    FlatSerial: '',
    FlatModel: '',
    TimeSpan: Date.parse(new Date()) / 1000,
    UserName,
    Password,
    RefererAccount
  })
}
