import {
    request
} from '@/utils/request'
import {
    isWeiXin
} from '@/utils'
import store from '@/store'
let retryCount = 0

const wx = window.wx

function getShareSign() {
    if (isWeiXin()) {
        request.post('/TopTeacherService.svc/GetWxSignature', {
                url: location.href.split('#')[0]
            })
            .then((data) => {
                data = data.Data;
                setShareConfig(data);
            }).catch((error) => {
                console.log(error);
            });
    }
}

function setShareConfig(data) {
    wx.config({
        debug: false,
        appId: data.appId,
        timestamp: data.timespan,
        nonceStr: data.nonceStr,
        signature: data.signature,
        jsApiList: [
            'onMenuShareTimeline',
            'onMenuShareAppMessage',
            'onMenuShareQQ',
            'onMenuShareQZone'
        ]
    })
    wx.error(function () {
        // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，
        // 也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
        retryCount++
        if (retryCount < 3) {
            getShareSign()
        }
    })
}

getShareSign()

export function setShareData(data) {
    wx.ready(function () {
        // 微信分享的数据
        retryCount = 0
        var shareData = {
            desc: (data && data.desc) || '龙门课堂',
            link: (data && data.link) || encodeURIComponent(location.href.split('#')[0]),
            imgUrl: (data && data.imgUrl) || 'https://sjkjkeyushijian-1257034971.cos.ap-shanghai.myqcloud.com/topTeacher/logo.png',
            title: (data && data.title) || '龙门课堂',
            success: function () {
                // 分享成功可以做相应的数据处理
                store.commit('shareComplate', true)
            },
            cancel: function () {
                store.commit('shareComplate', true)
            }
        }
        // 分享微信朋友圈
        wx.onMenuShareTimeline(shareData)
        wx.onMenuShareAppMessage(shareData)
        wx.onMenuShareQQ(shareData)
        wx.onMenuShareQZone(shareData)
    })
}
