import {
    request
} from '@/utils/request'
import {
    TOP_SERVICE,
} from './common'

export function getBalance() {
    return request.post(`${TOP_SERVICE}GetBalance`)
}
export function getTopTeacherBalanceDetail(pageData) {
    return request.post(`${TOP_SERVICE}GetTopTeacherBalanceDetail`, {
        ...pageData
    })
}
export function getTopTeacherRefererBalanceDetail(pageData) {
    return request.post(`${TOP_SERVICE}GetTopTeacherRefererBalanceDetail`, {
        ...pageData
    })
}
