const app = {
  state: {
    isLoading: false,
    toast: '',
    scrollTop: 0
  },
  mutations: {
    updateLoadingStatus (state, payload) {
      state.isLoading = payload.isLoading
    },
    updatePosition (state, scrollTop) {
      state.scrollTop = scrollTop
    },
    showMessage: (state, toast) => {
      state.toast = toast
    }
  },
  actions: {
    showMessage ({
      commit
    }, toast) {
      commit('showMessage', toast)
    }
  }
}

export default app
