/* eslint-disable no-unused-vars */
import {
  getCourse,
  getAdvertisList,
  getStageGrade
} from '@/api/common'
const common = {
  state: {
    courses: [],
    stages: []
  },
  mutations: {
    addCourses: (state, courses) => {
      state.courses = courses
    },
    addStages: (state, stages) => {
      state.stages = stages
    }
  },
  actions: {
    getCourse({
      commit
    }) {
      return new Promise((resolve, reject) => {
        getCourse()
          .then((data) => {
            let courses = data.GetCourseInfoDto
            courses.map(course => {
              course.key = course.CourseInfoID
              course.value = course.Name
            })
            courses.unshift({
              key: '0',
              value: '全部'
            })
            commit('addCourses', courses)
            resolve(courses)
          })
          .catch((err) => {
            reject(err)
          })
      })
    },
    getAdvertisList({
      commit
    }) {
      return new Promise((resolve, reject) => {
        getAdvertisList()
          .then((data) => {
            resolve(data.AdvertisInfoDto)
          })
          .catch((err) => {
            reject(err)
          })
      })
    },
    getStageGrade({
      commit
    }) {
      return new Promise((resolve, reject) => {
        getStageGrade()
          .then((data) => {
            let stages = data.GetStageInfoDto
            stages.map(stage => {
              stage.key = stage.StageID
              stage.value = stage.Name
            })
            stages.unshift({
              key: '0',
              value: '全部'
            })
            commit('addStages', stages)
            resolve(stages)
          })
          .catch((err) => {
            reject(err)
          })
      })
    }
  }
}

export default common
