export function isDef(value) {
  return value !== undefined && value !== null
}

export const isIos = (/(iPhone|iPod|iPad);?/i).test(navigator.userAgent.toLowerCase())

export const isAndroid = /android/.test(navigator.userAgent.toLowerCase())

export function isObj(x) {
  const type = typeof x
  return x !== null && (type === 'object' || type === 'function')
}

export function isPI(val) {
  const regPI = /^[0-9]*[1-9][0-9]*$/
  return regPI.test(val)
}

export function isPoneAvailable(str) {
  var myreg = /^[1][3,4,5,7,8][0-9]{9}$/
  if (!myreg.test(str)) {
    return false
  } else {
    return true
  }
}

export function isNumber(val) {
  const regPos = /^\d+(\.\d+)?$/ // 非负浮点数
  return regPos.test(val)
}

//判断是否是微信浏览器的函数
export function isWeiXin() {
  //window.navigator.userAgent属性包含了浏览器类型、版本、操作系统类型、浏览器引擎类型等信息，这个属性可以用来判断浏览器类型
  var ua = window.navigator.userAgent.toLowerCase();
  //通过正则表达式匹配ua中是否含有MicroMessenger字符串
  if (ua.match(/MicroMessenger/i) == 'micromessenger') {
    return true;
  } else {
    return false;
  }
}

export function debounce(func, wait = 500, immediate) {
  let timeout, args, context, timestamp, result

  const later = function () {
    // 据上一次触发时间间隔
    const last = +new Date() - timestamp

    // 上次被包装函数被调用时间间隔last小于设定时间间隔wait
    if (last < wait && last > 0) {
      timeout = setTimeout(later, wait - last)
    } else {
      timeout = null
      // 如果设定为immediate===true，因为开始边界已经调用过了此处无需调用
      if (!immediate) {
        result = func.apply(context, args)
        if (!timeout) context = args = null
      }
    }
  }

  return function (...args) {
    context = this
    timestamp = +new Date()
    const callNow = immediate && !timeout
    // 如果延时不存在，重新设定延时
    if (!timeout) timeout = setTimeout(later, wait)
    if (callNow) {
      result = func.apply(context, args)
      context = args = null
    }

    return result
  }
}

export function throttle(method, mustRunDelay) {
  let timer
  let args = arguments
  let start
  return function loop() {
    let self = this
    let now = Date.now()
    if (!start) {
      start = now
    }
    if (timer) {
      clearTimeout(timer)
    }
    if (now - start >= mustRunDelay) {
      method.apply(self, args)
      start = now
    } else {
      timer = setTimeout(function () {
        loop.apply(self, args)
      }, 50)
    }
  }
}

export function trim(str) {
  return str.replace(/\\r\\n/gm, '<br/>')
}

export function encodeUrl(json) {
  return Object.keys(json).map(function (key) {
    return encodeURIComponent(key) + '=' + encodeURIComponent(json[key])
  }).join('&')
}

