import Vue from 'vue'
import VueRouter from 'vue-router'
import classroomRouter from './module/classroom'
import mineRouter from './module/mine'
import loginRegister from './module/login-register'
import explainRouter from './module/explain'
Vue.use(VueRouter)

const routes = [
  classroomRouter,
  mineRouter,
  loginRegister,
  explainRouter,
  {
    path: '/redirect/:path*',
    name: 'redirect',
    component: () => import('@/views/redirect').then(m => m.default)
  },
  {
    path: '/qr-code',
    name: 'QrCode',
    component: () => import('@/views/qr-code').then(m => m.default),
  },
  {
    path: '*',
    redirect: 'classroom'
  }
]
export default new VueRouter({
  routes,
  scrollBehavior: (to, from, savePosition) => {
    if (savePosition) {
      return savePosition
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  }
})
