import {
  mapGetters
} from 'vuex'
export default {
  data() {
    return {
      role: 'p'
    }
  },
  computed: {
    ...mapGetters(['userInfo']),
    isS() {
      return this.role === 's'
    }
  },
  watch: {
    $route() {
      const role = this.$route.query.role || 'p'
      localStorage.setItem('role', role)
      this.role = role
    }
  },
  methods: {
    login() {
      this.$router.replace({
        name: 'Login',
        query: {
          redirect: this.$route.fullPath
        }
      })
    },
    jump(name, query = {}, params = {}, replace = false) {
      if (replace) {
        this.$router.replace({
          name,
          query,
          params
        })
      } else {
        this.$router.push({
          name,
          query,
          params
        })
      }
    },
    back() {
      const routeName = this.$route.name
      if (this.isS) {
        this.$router.back()
        return false
      }
      if (routeName === 'ClassroomReferer') {
        this.$router.replace({
          name: 'Classroom',
          query: this.$route.query
        })
      } else {
        this.$router.back()
      }
    }
  }
}
