import Layout from '@/layout'
const loginRegisterRouter = {
  path: '/',
  component: Layout,
  children: [{
    path: 'login',
    name: 'Login',
    component: () => import('@/views/login').then(m => m.default),
    meta: {
      navbar: {
        left: true,
        title: '登录'
      },
      showBottom: false
    }
  },{
    path: 'register',
    name: 'Register',
    component: () => import('@/views/register').then(m => m.default),
    meta: {
      navbar: {
        left: true,
        title: '注册'
      },
      showBottom: false
    }
  }]
}

export default loginRegisterRouter
