import {
  request
} from '@/utils/request'

export const TOP_SERVICE = '/TopTeacherService.svc/'
export const COMMON_SERVICE = '/CommonService.svc/'

export function getCourse () {
  return request.post(`${COMMON_SERVICE}com_GetCourse`)
}
export function getAdvertisList () {
  return request.post(`${TOP_SERVICE}GetAdvertisList`, {
    Type: 2 // 类型 1：学生 2：老师
  })
}
// 获取阶段及年级
export function getStageGrade () {
  return request.post(`${COMMON_SERVICE}GetStageGrade`)
}
