/* eslint-disable no-unused-vars */
import {
  login,
  getUserToken,
  updateUserInfo,
  mobileCheck,
  getVerifyCode,
  verifyCode,
  regedit
} from '@/api/login-register'
import {
  setUserInfo,
  getUserInfo,
  setToken,
  getToken,
  setUsername,
  setPassword,
  getUsername,
  getPassword
} from '@/utils/ls'
const user = {
  state: {
    username: getUsername(),
    password: getPassword(),
    userInfo: getUserInfo(),
    token: getToken()
  },
  mutations: {
    setUserInfo: (state, userInfo) => {
      state.userInfo = userInfo
    },
    setToken: (state, token) => {
      state.token = token
    },
    setUsername: (state, username) => {
      state.username = username
    },
    setPassword: (state, password) => {
      state.password = password
    }
  },
  actions: {
    login({
      commit
    }, loginData) {
      return new Promise((resolve, reject) => {
        login(loginData).then((data) => {
          let userInfo = data.Data
          console.log(loginData)
          commit('setUsername', loginData.UserName)
          commit('setPassword', loginData.Password)
          commit('setUserInfo', userInfo)
          commit('setToken', data.Token)
          setUsername(loginData.UserName)
          setPassword(loginData.Password)
          setUserInfo(userInfo)
          setToken(data.Token)
          resolve(userInfo)
        }).catch((err) => {
          reject(err)
        })
      })
    },
    // 获取 token
    getUserToken({
      commit
    }, userInfo) {
      return new Promise((resolve, reject) => {
        getUserToken(userInfo)
          .then(data => {
            commit('setToken', data.Token)
            setToken(data.Token)
            resolve()
          })
          .catch(error => {
            reject(error)
          })
      })
    },
    updateUserInfo({
      commit
    }, userData) {
      return new Promise((resolve, reject) => {
        updateUserInfo(userData).then(() => {
          let userInfo = getUserInfo()
          for (const key in userInfo) {
            if (userInfo.hasOwnProperty(key)) {
              if (key === userData[0]) {
                userInfo[key] = userData[1]
                commit('setUserInfo', userInfo)
                setUserInfo(userInfo)
                break
              }
            }
          }
          resolve()
        }).catch((err) => {
          reject(err)
        })
      })
    },
    logout({
      commit
    }) {
      return new Promise((resolve) => {
        commit('setUserInfo', undefined)
        commit('setToken', undefined)
        localStorage.clear()
        resolve()
      })
    },
    mobileCheck({
      commit
    }, moblie) {
      return new Promise((resolve, reject) => {
        mobileCheck(moblie).then(() => {
          resolve()
        }).catch((err) => {
          reject(err)
        })
      })
    },
    getVerifyCode({
      commit
    }, moblie) {
      return new Promise((resolve, reject) => {
        getVerifyCode(moblie).then(() => {
          resolve()
        }).catch((err) => {
          reject(err)
        })
      })
    },
    verifyCode({
      commit
    }, moblie) {
      return new Promise((resolve, reject) => {
        verifyCode(moblie).then(() => {
          resolve()
        }).catch((err) => {
          reject(err)
        })
      })
    },
    regedit({
      commit
    }, userData) {
      return new Promise((resolve, reject) => {
        regedit(userData).then(() => {
          resolve()
        }).catch((err) => {
          reject(err)
        })
      })
    },
  }
}
export default user
