import Layout from '@/layout'
const mineRouter = {
  path: '/mine',
  component: Layout,
  redirect: '',
  children: [{
    path: '',
    name: 'Mine',
    component: () => import('@/views/mine').then(m => m.default),
    meta: {
      navbar: {
        left: false,
        title: '我的',
        right: {
          icon: 'lm-shezhi',
          action: 'settings'
        }
      },
      showBottom: true
    }
  }, {
    path: 'referer',
    name: 'Referer',
    component: () => import('@/views/mine/referer').then(m => m.default),
    meta: {
      requireAuth: true,
      navbar: {
        left: true,
        title: '我的推广'
      },
      showBottom: false
    }
  }, {
    path: 'referer-detail',
    name: 'RefererDetail',
    component: () => import('@/views/mine/referer/detail').then(m => m.default),
    meta: {
      requireAuth: true,
      navbar: {
        left: true,
        title: '推广详情'
      },
      showBottom: false
    }
  }, {
    path: 'balance',
    name: 'Balance',
    component: () => import('@/views/mine/balance').then(m => m.default),
    meta: {
      requireAuth: true,
      navbar: {
        left: true,
        title: '我的收益'
      },
      showBottom: false
    }
  }, {
    path: 'settings',
    name: 'Settings',
    component: () => import('@/views/mine/settings').then(m => m.default),
    meta: {
      requireAuth: true,
      navbar: {
        left: true,
        title: '设置'
      },
      showBottom: false
    }
  }, {
    path: 'settings/update',
    name: 'UserUpdate',
    component: () => import('@/views/mine/settings/update').then(m => m.default),
    meta: {
      navbar: {
        left:true,
        right: {
          name: '完成',
          action: 'updateUser'
        }
      },
      showBottom: false,
      requireAuth: true
    },
    beforeEnter: (to, from, next) => {
      let title = to.query.title
      if (title) {
        to.meta.navbar.title = title
      }
      next()
    }
  }]
}

export default mineRouter
