/* axios 的封装 */
import store from '@/store'
import axios from 'axios'
import {
  Toast
} from 'vant'
let getTokenCount = 0
// 创建请求头部对象
const headers = {
  'Content-Type': 'application/json'
}
// 创建 axios 实例
const instance = axios.create({
  // 请求超时时间
  timeout: 4000,
  // 请求头
  headers
})
instance.interceptors.request.use(
  request => {
    store.commit('updateLoadingStatus', {
      isLoading: true
    })
    request.headers.Token = store.state.loginRegister.token
    return request
  },
  error => {
    console.log(error)
    store.commit('updateLoadingStatus', {
      isLoading: false
    })
  }
)
/**
 * 响应数据拦截
 */
instance.interceptors.response.use(
  response => {
    store.commit('updateLoadingStatus', {
      isLoading: false
    })
    let data = response.data
    console.log(data)
    switch (data.Code || data.code) {
      case 1:
        getTokenCount = 0
        return data
      case 2:
        getTokenCount = 0
        var requestUrl = response.request.responseURL.split('/')
        if (requestUrl[requestUrl.length - 1] === 'Login') {
          return data
        }
        Toast(data.message || data.Message)
        return Promise.reject(response)
      case -6:
      case -7:
      case -8:
      case -9:
        // token 无效 状态设为 -1 单独处理
        response.status = '-1'
        return Promise.reject(response)
      default:
        return Promise.reject(response)
    }
  },
  error => {
    store.commit('updateLoadingStatus', {
      isLoading: false
    })
    if (error.response) {
      switch (error.response.status) {
        case 400:
          error.message = '请求错误'
          break
        case 401:
          error.message = '未授权，请登录'
          break
        case 403:
          error.message = '拒绝访问'
          break
        case 404:
          error.message = `请求地址出错: ${error.response.config.url}`
          break
        case 408:
          error.message = '请求超时'
          break
        case 500:
          error.message = '服务器内部错误'
          break
        case 501:
          error.message = '服务未实现'
          break
        case 502:
          error.message = '网关错误'
          break
        case 503:
          error.message = '服务不可用'
          break
        case 504:
          error.message = '网关超时'
          break
        case 505:
          error.message = 'HTTP版本不受支持'
          break
        default:
          error.message = '网络错误'
      }
    }
    Toast(error.message)
    return Promise.reject(error)
  }
)

instance.interceptors.response.use(
  undefined,
  error => {
    if (error.status === '-1' && getTokenCount <= 4) {
      let UserName = store.state.loginRegister.username
      let Password = store.state.loginRegister.password
      if (UserName && Password) {
        getTokenCount++
        return new Promise((resolve) => {
          store.dispatch('getUserToken', {
            UserName,
            Password
          }).then(() => {
            resolve(instance(error.config))
          }).catch(() => {
            logOut()
          })
        })
      } else {
        if (error.data && error.data.message) {
          Toast(error.data.message)
        }
        logOut()
      }
    } else {
      if (error.data && (error.data.message || error.data.Message)) {
        Toast(error.data.message || error.data.Message)
      }
      return Promise.reject(error)
    }
  }
)

// 创建 request 对象，里面定义了 post get 等请求
const request = {
  /*
   * post 请求
   * @url 请求地址
   * @data 请求数据
   */
  post(url, data) {
    // 打印 请求地址和请求数据
    console.log(`url: ${url} , data: ${JSON.stringify(data || {})}`)
    return instance({
      method: 'post',
      url: '/api/ft/' + url,
      data: JSON.stringify(data || {}) || {}
    })
  }
}
export {
  request
}

// 登出系统
function logOut() {
  store.dispatch('logout').then(() => {
    location.reload()
  })
}
